import React, { Component } from 'react';
import {Link} from 'react-router-dom';
import { Card, CardBody, CardHeader, Col, Row, Button, Form, Input, FormGroup, Label, /*Alert,*/ Media} from 'reactstrap';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import commonService from '../../../core/services/commonService';
//import AutoCompletePlaces from '../../../core/google-map/AutoCompletePlaces';
import Checkbox from "../../../core/components/Checkbox";
import Loader from '../../Loader/Loader';
import './DoctorsDetails.css';

//var eventData = "";
const weekArr = [{day: 1, dayName: 'Mon', isSelected: false, timeSlots: [{startTime: "", endTime: ""}]}, {day: 2, dayName: 'Tue', isSelected: false, timeSlots: [{startTime: "", endTime: ""}]}, {day: 3, dayName: 'Wed', isSelected: false, timeSlots: [{startTime: "", endTime: ""}]}, 
{day: 4, dayName: 'Thu', isSelected: false, timeSlots: [{startTime: "", endTime: ""}]}, {day: 5, dayName: 'Fri', isSelected: false, timeSlots: [{startTime: "", endTime: ""}]}, {day: 6, dayName: 'Sat', isSelected: false, timeSlots: [{startTime: "", endTime: ""}]}, {day: 0, dayName: 'Sun', isSelected: false, timeSlots: [{startTime: "", endTime: ""}]}] ;
class DoctorsDetails extends Component {
  constructor(props){
    super(props);
    this.state = {
      buttonProcessing: false,
      loading: true,
      errors: {},
      visible: false,
      profileImage: '',
      imagePreview: "",
      documentsImages:[],
      uploadedDocumentsImages: [],
      formProccessing: false,
      formField: {},
      checkboxes: weekArr,
      dataTableItem: [],
      categoryList: [],
      selectedServices: [],
      superSpecializationList: [],
      specializationList: [],
      selectedSpecialization: [],
      selectedSuperSpecialization: [],
    };
    this.getCategoryList = this.getCategoryList.bind(this);
    this.submitHandler = this.submitHandler.bind(this);
    
  }
  componentDidMount() { 
    const { match: { params } } = this.props;
    if(params.doctorsId !== undefined && params.doctorsId !=="") {
      this.setState({doctorsId: params.doctorsId});
      this.getCategoryList();
      this.getDoctorProfile(params.doctorsId);
    }
    else
      this.props.history.push('/admin/manage-investigator/investigator');
    this.getSpecialization();
    this.getSuperSpecialization();
    
  }

  getSpecialization() {
    let filterParmas = '?isSuperSpecialization='+false;
    commonService.getAPIWithAccessToken('specialization'+filterParmas)
        .then( res => {
          
           
          if ( undefined === res.data.data || !res.data.status ) {            
            toast.error(res.data.message);    
            return;
          }   

          this.setState({specializationList: res.data.data});     
         
        } )
        .catch( err => {   
                
          if(err.response !== undefined && err.response.status === 401) {
            localStorage.clear();
            this.props.history.push('/login');
          }
          else {
            
            toast.error(err.message);    
          }
        } )
  }

  getSuperSpecialization(){
    let filterParmas = '?isSuperSpecialization='+true;
    commonService.getAPIWithAccessToken('specialization'+filterParmas)
        .then( res => {
          
           
          if ( undefined === res.data.data || !res.data.status ) {            
            toast.error(res.data.message);    
            return;
          }   

          this.setState({superSpecializationList: res.data.data});     
         
        } )
        .catch( err => {   
                
          if(err.response !== undefined && err.response.status === 401) {
            localStorage.clear();
            this.props.history.push('/login');
          }
          else {
            
            toast.error(err.message);    
          }
        } )
  }

  /* Input Field On changes*/
  changeHandler = event => {
    const name = event.target.name;
    const value = event.target.value;
    const formField = this.state.formField
    formField[name] = value;
    this.setState({ formField: formField });
  };

  getDoctorProfile(doctorsId){
    commonService.getAPIWithAccessToken('doctors/profile/'+doctorsId)
      .then( res => {
        console.log(res);
        this.setState({loading: false});   
        if ( undefined === res.data.data || !res.data.status ) {
          this.setState( {  loading: false } );
          toast.error(res.data.message);    
          return;
        }   
        const profileInfo = res.data.data;
        let formField = this.state.formField;
        formField.first_name = profileInfo.firstName;
        formField.last_name = profileInfo.lastName || "";
        formField.phoneNumber = profileInfo.phoneNumber || "";
        formField.address = profileInfo.address || "";
        formField.description = profileInfo.description || "";
        let services = profileInfo.services || [];
        let selectedServices = services.map(item => item.categoryId);
        let specialization = profileInfo.specialization || [];
        let selectedSpecialization = specialization.map(item => item.specializationId);
        let superSpecialization = profileInfo.superSpecialization || [];
        let selectedSuperSpecialization = superSpecialization.map(item => item.specializationId);
        let selectedCheckboxes = profileInfo.availability || [];
        let weekDataArray = [];
        let profileImage = profileInfo.profileImage || "";
        let imagePreview = profileInfo.profileImage || "";
        let documentsImages = profileInfo.documentsImages || [];
        weekArr.forEach((weekInfo, index) => {
          let dayInfo =selectedCheckboxes.filter(function(item) { return item.day === weekInfo.day;});
          if(dayInfo.length > 0 ){
            weekInfo.timeSlots = dayInfo[0].timeSlots.length > 0 ? dayInfo[0].timeSlots :[{startTime: "", endTime: ""}];
            weekInfo.isSelected = dayInfo[0].isOpen;
            weekDataArray.push(weekInfo);
          }
          else
            weekDataArray.push(weekInfo);
        });
        this.setState({formField: formField, profileImage: profileImage, imagePreview: imagePreview, uploadedDocumentsImages: documentsImages, selectedServices: selectedServices, selectedSpecialization: selectedSpecialization, selectedSuperSpecialization: selectedSuperSpecialization, checkboxes: weekDataArray });     
       
      } )
      .catch( err => {   
        this.setState({loading: false});   
        if(err.response !== undefined && err.response.status === 401) {
          localStorage.clear();
          this.props.history.push('/login');
        }
        else           
          toast.error(err.message);    
        
      } )
  }
  getCategoryList() {

    commonService.getAPIWithAccessToken('category')
      .then( res => {
        console.log(res);
         
        if ( undefined === res.data.data || !res.data.status ) {
          this.setState( {  loading: false } );
          toast.error(res.data.message);    
          return;
        }   
        
        this.setState({categoryList: res.data.data});     
       
      } )
      .catch( err => {   
              
        if(err.response !== undefined && err.response.status === 401) {
          localStorage.clear();
          this.props.history.push('/login');
        }
        else           
          toast.error(err.message);    
        
      } )
  }


  submitHandler (event) {
    event.preventDefault();
    event.target.className += " was-validated";
    if(this.validateForm()){
      return false;
    }
    this.setState( { formProccessing: true}, () => {
      const formInputField = this.state.formField;
      const formData = new FormData();
      formData.append('organizationId', this.state.doctorsId);
      formData.append('firstName', formInputField.first_name);
      formData.append('lastName', formInputField.last_name);
      formData.append('phoneNumber', formInputField.phoneNumber);
      formData.append('description', formInputField.description);
      formData.append('address', formInputField.address || "");
      formData.append('latitude', formInputField.latitude || "");
      formData.append('longitude', formInputField.longitude || "");

      if(this.state.profileImage !== "")
        formData.append('profileImage', this.state.profileImage);
      
      for(let i =0; i < this.state.documentsImages.length; i++){
        formData.append('documentsImages', this.state.documentsImages[i]);
      }
      
      for(let i =0; i < this.state.checkboxes.length; i++){
        if(this.state.checkboxes[i].isSelected)
          formData.append('availability', JSON.stringify({day: this.state.checkboxes[i].day, dayName: this.state.checkboxes[i].dayName, isOpen: true, timeSlots: this.state.checkboxes[i].timeSlots}));
        else
          formData.append('availability', JSON.stringify({day: this.state.checkboxes[i].day, dayName: this.state.checkboxes[i].dayName, isOpen: false, timeSlots: []}));
      }
      
      for(let i =0; i < this.state.selectedServices.length; i++){
        
          formData.append('services['+i+']', this.state.selectedServices[i]);
      }

      for(let i =0; i < this.state.selectedSpecialization.length; i++){
        
          formData.append('specialization['+i+']', this.state.selectedSpecialization[i]);
      }

      for(let i =0; i < this.state.selectedSuperSpecialization.length; i++){
        
          formData.append('superSpecialization['+i+']', this.state.selectedSuperSpecialization[i]);
      }
      
      commonService.putAPIWithAccessToken('doctors/info', formData)
      .then( res => {
        
        if ( undefined === res.data.data || !res.data.status ) { 
          this.setState( { formProccessing: false} );
          toast.error(res.data.message);
          return;
        } 
        this.props.history.push('/admin/manage-investigator/investigator');
        toast.success(res.data.message);
      } )
      .catch( err => { 
        
        if(err.response !== undefined && err.response.status === 401) {
          localStorage.clear();
          this.props.history.push('/login');
        }else
          this.setState( { formProccessing: false } );
          toast.error(err.message);
      } )
      
    } );
    
  };


  /* Validate Form */
  validateForm() {
    let formValid = false;
    let formField = this.state.formField; 
    let errors = {};
    if(formField.first_name === undefined || formField.first_name.trim() === ""){
      formValid = true;
      errors['firstName'] = "First name field required!";
    }
    /*if(this.state.selectedServices.length === 0 ){
      formValid = true;
      errors['services'] = "Choose atleast single specialization";
    }*/
    if(this.state.selectedSpecialization.length === 0 ){
      formValid = true;
      errors['specialization'] = "Choose atleast single specialization";
    }
    if(this.state.selectedSuperSpecialization.length === 0 ){
      formValid = true;
      errors['superSpecialization'] = "Choose atleast single super specialization";
    }
    let checkboxes = this.state.checkboxes;
    let selectedAvailability = checkboxes.filter(function(item) { return item.isSelected;});
    if(selectedAvailability.length > 0 ){
      selectedAvailability.forEach((dayInfo, index) => {        
        const selectedTimeSlots = dayInfo.timeSlots.filter(function(item){ return item.startTime !== "" && item.endTime !== "";});
        if(selectedTimeSlots.length === 0){
          formField = true;
          errors['availability'] = "Selected Availability Day should have time slot";
        }
      })
    }

    this.setState({errors:  errors});
    return formValid;
    
  }

  handleAvlChange = e => {
    //const name = e.target.name;
    let checkboxes = this.state.checkboxes;
    checkboxes[e.target.value].isSelected = e.target.checked;
    this.setState({checkboxes: checkboxes});
  };

  handleAvlCatChange = e => {
    //const name = e.target.name;
    let selectedServices = this.state.selectedServices;
    if(e.target.checked) 
      selectedServices.push(e.target.value);
    else
      selectedServices.splice(selectedServices.indexOf(e.target.value), 1);
    
    this.setState({selectedServices: selectedServices});
  };

  handleSpecializationChange = e => {
    //const name = e.target.name;
    let selectedSpecialization = this.state.selectedSpecialization;
    if(e.target.checked) 
      selectedSpecialization.push(e.target.value);
    else
      selectedSpecialization.splice(selectedSpecialization.indexOf(e.target.value), 1);
    
    this.setState({selectedSpecialization: selectedSpecialization});
  };

  handleSuperSpecializationChange = e => {
    //const name = e.target.name;
    let selectedSuperSpecialization = this.state.selectedSuperSpecialization;
    if(e.target.checked) 
      selectedSuperSpecialization.push(e.target.value);
    else
      selectedSuperSpecialization.splice(selectedSuperSpecialization.indexOf(e.target.value), 1);
    
    this.setState({selectedSuperSpecialization: selectedSuperSpecialization});
  };

  changeTimeHandler = event => {
    const targetName = event.target.name.split('-');
    if(targetName.length > 1) {
      let checkboxes = this.state.checkboxes;
      let timeSlots = checkboxes[targetName[1]].timeSlots;
      if(targetName[0] === "openTime")
        timeSlots[targetName[2]].startTime = event.target.value;
      else
        timeSlots[targetName[2]].endTime = event.target.value;
      checkboxes[targetName[1]].timeSlots = timeSlots;
      this.setState({checkboxes: checkboxes});
    }
    
  }
    
  addMoreTimeSlots = (day) => {
    let checkboxes = this.state.checkboxes;
    checkboxes[day].timeSlots.push({startTime: "", endTime: ""});
    this.setState({checkboxes: checkboxes});
  } 

  removeTimeSlots = (day, timeSlotIndex) => {
    let checkboxes = this.state.checkboxes;
    let timeSlots = checkboxes[day].timeSlots;
    timeSlots.splice(timeSlotIndex, 1)
    checkboxes[day].timeSlots = timeSlots;
    this.setState({checkboxes: checkboxes});
    
  }

  handleImageChange = (e) => {
    const targetFile = e.target.files[0];
    var reader = new FileReader();
    let currentObj = this;
    reader.onload = function(e) {
        currentObj.setState({imagePreview: e.target.result});
      }
      reader.readAsDataURL(targetFile);
    this.setState({
      profileImage: targetFile
    })
  };

  deleteDocumentsImages = (index, docType) => {
    this.setState( { loading: true}, () => {
      commonService.deleteAPIWithAccessToken('doctors/documents', {organizationId: this.state.doctorsId, index:index})
        .then( res => {
          console.log(res);
           
          if ( undefined === res.data.data || !res.data.status ) {
            this.setState( {  loading: false } );
            toast.error(res.data.message);  
            this.props.history.push('/user');  
            return;
          }   
          let uploadedDocumentsImages = this.state.uploadedDocumentsImages;
          uploadedDocumentsImages.splice(index, 1);
          this.setState({loading:false, uploadedDocumentsImages: uploadedDocumentsImages});     
         
        } )
        .catch( err => {         
          if(err.response !== undefined && err.response.status === 401) {
            localStorage.clear();
            this.props.history.push('/login');
          }
          else {
            this.setState( { loading: false } );
            toast.error(err.message);    
          }
        } )
    } )
  }
 
  onDocumentsImageChange = event => {   
    this.setState({
      documentsImages: event.target.files,
    });
  };

  render() {
    
    const { loading, errors, formProccessing, imagePreview, uploadedDocumentsImages } = this.state;

    let loaderElement = '';    
    let changeStatusBtn = "";
    
    if(loading)        
      loaderElement = <Loader />
    
    return (
      <div className="animated fadeIn">
        
        {loaderElement}
        <Card>
          <CardHeader>
            <Row>
            <Col md="3" className="mainHeading">
              <strong className="mr-5">Investigator Info</strong>
            </Col>
            </Row>
          </CardHeader>
          <CardBody>
            
            <Form onSubmit={this.submitHandler} noValidate className="oep-form">
            
              <Row>
              
                <Col md={"6"}>  
                  <FormGroup> 
                    <Label htmlFor="first_name">First Name</Label>            
                    <Input type="text" placeholder="First Name *" invalid={errors['firstName'] !== undefined && errors['firstName'] !== ""} id="first_name" name="first_name" value={this.state.formField.first_name} onChange={this.changeHandler} required />
                    <div className="invalid-feedback">
                      {errors['firstName']}
                    </div>
                  </FormGroup>
                </Col>
                <Col md={"6"}>  
                  <FormGroup> 
                    <Label htmlFor="last_name">Last Name</Label>            
                    <Input type="text" placeholder="Last Name" id="last_name" name="last_name" value={this.state.formField.last_name} onChange={this.changeHandler}  />
                    <div className="invalid-feedback">
                      {errors['lastName']}
                    </div>
                  </FormGroup>
                </Col> 
                
                
                
                <Col md={"6"}>  
                  <FormGroup> 
                    <Label htmlFor="phoneNumber">Contact Number</Label>            
                    <Input type="text" placeholder="Contact Number " id="phoneNumber" name="phoneNumber" value={this.state.formField.phoneNumber} onChange={this.changeHandler}  />
                  </FormGroup>
                </Col>
                <Col md={"6"}>  
                  <FormGroup> 
                    <Label htmlFor="address">Address</Label>            
                    <Input type="text" placeholder="Address" id="address" name="address" value={this.state.formField.address} onChange={this.changeHandler}  />
                    
                  </FormGroup>
                </Col> 
                <Col md={"12"}>
                  <FormGroup> 
                    <Label htmlFor="description">Description</Label>
                    <Input type="textarea" placeholder="Investigator details" id="description" name="description" value={this.state.formField.description} onChange={this.changeHandler} />
                  </FormGroup>
                </Col>
                
                <Col md={"12"}>
                  <FormGroup>
                    <Label htmlFor="avl">Super Specialization </Label><br/>
                    <Row>
                    {this.state.superSpecializationList.map((specializationInfo, index) => 
                      <Col md= {5} > 
                        <FormGroup  check inline  key={index}>
                          <Label check>
                          <Checkbox
                            label={specializationInfo.specializationName}
                            isSelected={ this.state.selectedSuperSpecialization.indexOf(specializationInfo.specializationId) > -1}
                            onCheckboxChange={this.handleSuperSpecializationChange}                            
                            value={ specializationInfo.specializationId}
                            key={index}
                          />
                          </Label>
                          
                        </FormGroup>
                      </Col>
                      )}
                      <div className="error-feedback">
                        {errors['superSpecialization']}
                      </div>
                    </Row>
                  </FormGroup>  
                </Col>
                <Col md={"12"}>
                  <FormGroup>
                    <Label htmlFor="avl">Specialization </Label><br/>
                    <Row>
                    {this.state.specializationList.map((specializationInfo, index) => 
                      <Col md= {5} > 
                        <FormGroup  check inline  key={index}>
                          <Label check>
                          <Checkbox
                            label={specializationInfo.specializationName}
                            isSelected={ this.state.selectedSpecialization.indexOf(specializationInfo.specializationId) > -1}
                            onCheckboxChange={this.handleSpecializationChange}                            
                            value={ specializationInfo.specializationId}
                            key={index}
                          />
                          </Label>
                          
                        </FormGroup>
                      </Col>
                      )}
                      <div className="error-feedback">
                        {errors['specialization']}
                      </div>
                    </Row>
                  </FormGroup>  
                </Col>
                <Col md={"12"}>
                  <FormGroup>
                    <Label htmlFor="avl">Availability </Label><br/>
                    <div className="error-feedback">
                      {errors['availability']}
                    </div>
                    <Row>
                    {weekArr.map((week, index) => 
                      <Col md= {5} > 
                        <FormGroup  check  key={index}>
                          <Label check>
                          <Checkbox
                            label={this.state.checkboxes[index].dayName}
                            isSelected={ this.state.checkboxes[index].isSelected}
                            onCheckboxChange={this.handleAvlChange}                            
                            value={ index}
                            key={this.state.checkboxes[index].day}
                          />
                          </Label>
                          { this.state.checkboxes[index].isSelected ? 
                            this.state.checkboxes[index].timeSlots.length > 0 ? 
                              <FormGroup className="opening-hours"> 
                                  <Label htmlFor="openTime">Opening Hours</Label><br/>
                                {
                                this.state.checkboxes[index].timeSlots.map((slotsInfo, slotIndex) =>                                
                                  <Row className="mb-1">
                                    <Col md={"5"}>
                                    <input type="time" id="openTime" className="form-control input-hour" name={`openTime-${index}-${slotIndex}`} value={slotsInfo.startTime} onChange={this.changeTimeHandler} />
                                    </Col>
                                    <Col md={"5"}>
                                    <input type="time" id="closeTime" className="form-control input-hour" name={`closeTime-${index}-${slotIndex}`} value={slotsInfo.endTime} onChange={this.changeTimeHandler} />
                                    </Col>
                                    <Col md={"1"} className="add-btn">
                                    {this.state.checkboxes[index].timeSlots.length -1 === slotIndex ? <span className="add-more-slots" title="Add more time slots" onClick = {this.addMoreTimeSlots.bind(this, index)}><i className="fa fa-plus"></i></span> :
                                    <span className="remove-more-slots" title="Remove time slots" onClick = {this.removeTimeSlots.bind(this, index, slotIndex)}><i className="fa fa-minus"></i></span>
                                    }
                                    </Col>
                                  </Row>
                                
                                )} 
                              </FormGroup>
                              :
                              <></>
                            

                            : <></>
                          }
                        </FormGroup>
                      </Col>
                      )}
                    </Row>
                  </FormGroup>  
                </Col>
                <Col md={"6"}>
                  <FormGroup> 
                    <Label htmlFor="defaultImage">Profile Image</Label>            
                    <Input type="file" id="defaultImage" name="defaultImage" className="form-control"  onChange={this.handleImageChange} />
                    <small>Image will be shown as a Investigator profile image.</small>
                  </FormGroup>              
                </Col>
                <Col md={"6"}>{imagePreview && <div className="previewDefaultImage"><img alt="default" width="100" className="img-fluid img-thumbnail" src={imagePreview} /></div>}</Col>
                
                <Col md={"6"}>
                  <FormGroup> 
                    <Label htmlFor="documents">Documents</Label>            
                    <Input type="file" id="documents" name="documents" className="form-control" multiple onChange={this.onDocumentsImageChange} />
                  </FormGroup> 
                </Col>
                <Col md={"6"}>
                  <div className="previewdocumentsImageArea row">
                      {uploadedDocumentsImages.map((imagesInfo, index) =>
                        <Media className="previewdocumentsImage col-md-2" key={index}>
                          <a target="_blank" rel="noopener noreferrer" href ={imagesInfo}>Preview</a> <i className="fa fa-times text-danger" onClick={() => {if(window.confirm('Are you sure, you want to delete this documents?')){ this.deleteDocumentsImages(index,'documents') };}}></i>
                        </Media>
                      )}
                  </div>             
                </Col>
                
              </Row>

              <Button color="primary" disabled={formProccessing} type="submit">{formProccessing ? 'Processing...' : 'Update Details' }</Button>
              &nbsp; 
              {changeStatusBtn}
              &nbsp;
              <Link className="btn btn-secondary" to='/admin/manage-investigator/investigator'>Cancel</Link>
              
            </Form> 
          </CardBody>
        </Card>
      </div>

    )
  }
}

export default DoctorsDetails;

